import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {fetchData} from "../../redux/data/dataActions";
import * as s from "../../styles/globalStyles";
import {connect} from "../../redux/blockchain/blockchainActions";
import {ResponsiveWrapper, StyledButton, StyledLink, StyledRoundButton, truncate} from "./styleComponent";
import PlusBtnDefault from "../../assets/img/btn_plus_default-sm.png";
import PlusBtnHover from "../../assets/img/btn_plus_hover-sm.png";
import RestBtnDefault from "../../assets/img/btn_rest_default-sm.png";
import RestBtnHover from "../../assets/img/btn_rest_hover-sm.png";
import GreenBtnDefault from "../../assets/img/gree_button_default-sm.png";
import GreenBtnHover from "../../assets/img/green_button_hover-sm.png";

const MintBox = () => {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(`Click buy to mint your NFT.`);
  const [mintAmount, setMintAmount] = useState(1);
  const [isHoverBtn, setIsHoverBtn] = useState(null);
  
  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });
  
  const claimNFTs = () => {
    let cost = CONFIG.WEI_COST;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit);;
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Minting your ${CONFIG.NFT_NAME}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `WOW, the ${CONFIG.NFT_NAME} is yours! go visit Opensea.io to view it.`
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };
  
  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };
  
  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 25) {
      newMintAmount = 25;
    }
    setMintAmount(newMintAmount);
  };
  
  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };
  
  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };
  
  useEffect(() => {
    getConfig();
  }, []);
  
  useEffect(() => {
    getData();
  }, [blockchain.account]);
  
  const onHoverBtnMouseMove = (typeOfMouse, typeOfBtn) => {
    if (typeOfMouse === "mousemove") {
      setIsHoverBtn(typeOfBtn);
    } else {
      setIsHoverBtn(null);
    }
  };
  return (
    <ResponsiveWrapper flex={1} test>
      <s.Container
        flex={2}
        jc={"center"}
        ai={"center"}
      >
        <s.TextTitle
          style={{
            textAlign: "center",
            fontSize: "clamp(10px, 3vw, 40px)",
            fontWeight: "bold",
            color: "var(--accent-text)",
          }}
        >
          {data.totalSupply} / {CONFIG.MAX_SUPPLY}
        </s.TextTitle>
        <s.TextDescription
          style={{
            textAlign: "center",
            color: "var(--primary-text)",
          }}
        >
          <StyledLink target={"_blank"} href={CONFIG.SCAN_LINK}>
            {truncate(CONFIG.CONTRACT_ADDRESS, 15)}
          </StyledLink>
        </s.TextDescription>
        {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
          <>
            <s.TextTitle
              style={{
                textAlign: "center",
                color: "var(--accent-text)"
              }}
            >
              The sale has ended.
            </s.TextTitle>
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "var(--accent-text)"
              }}
            >
              You can still find {CONFIG.NFT_NAME} on
            </s.TextDescription>
            <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
              {CONFIG.MARKETPLACE}
            </StyledLink>
          </>
        ) : (
          <>
            <s.TextTitle
              style={{
                textAlign: "center",
                color: "var(--accent-text)"
              }}
            >
              1 {CONFIG.SYMBOL} costs {CONFIG.DISPLAY_COST}{" "}
              {CONFIG.NETWORK.SYMBOL}.
            </s.TextTitle>
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "var(--accent-text)"
              }}
            >
              Excluding gas fees.
            </s.TextDescription>
            {blockchain.account === "" ||
            blockchain.smartContract === null ? (
              <s.Container ai={"center"} jc={"center"}>
                <s.TextDescription
                  style={{
                    textAlign: "center",
                    color: "var(--accent-text)",
                  }}
                >
                  Connect to the {CONFIG.NETWORK.NAME} network
                </s.TextDescription>
                {blockchain.errorMsg !== "" ? (
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      {blockchain.errorMsg}
                    </s.TextDescription>
                  </>
                ) : null}
              </s.Container>
            ) : (
              <>
                <s.TextDescription
                  style={{
                    textAlign: "center",
                    color: "var(--accent-text)",
                  }}
                >
                  {feedback}
                </s.TextDescription>
                <s.SpacerMedium/>
                <s.Container ai={"center"} jc={"center"} fd={"row"}>
                  <StyledRoundButton
                    style={{lineHeight: 0.4}}
                    disabled={claimingNft ? 1 : 0}
                    onClick={(e) => {
                      e.preventDefault();
                      decrementMintAmount();
                    }}
                    onMouseMove={(e) => onHoverBtnMouseMove(e.type, "-")}
                    onMouseLeave={(e) => onHoverBtnMouseMove(e.type)}
                  >
                    <img src={isHoverBtn === "-" ? RestBtnHover : RestBtnDefault} alt=""/>
                  </StyledRoundButton>
                  <s.SpacerMedium/>
                  <s.TextDescription
                    style={{
                      textAlign: "center",
                      color: "var(--accent-text)",
                    }}
                  >
                    {mintAmount}
                  </s.TextDescription>
                  <s.SpacerMedium/>
                  <StyledRoundButton
                    disabled={claimingNft ? 1 : 0}
                    onClick={(e) => {
                      e.preventDefault();
                      incrementMintAmount();
                    }}
                    onMouseMove={(e) => onHoverBtnMouseMove(e.type, "+")}
                    onMouseLeave={(e) => onHoverBtnMouseMove(e.type)}
                  >
                    <img src={isHoverBtn === "+" ? PlusBtnHover : PlusBtnDefault} alt=""/>
                  </StyledRoundButton>
                </s.Container>
                <s.SpacerSmall/>
                <s.Container ai={"center"} jc={"center"} fd={"row"}>
                  <StyledButton
                    disabled={claimingNft ? 1 : 0}
                    onClick={(e) => {
                      e.preventDefault();
                      claimNFTs();
                      getData();
                    }}
                    onMouseMove={(e) => onHoverBtnMouseMove(e.type, "submit")}
                    onMouseLeave={(e) => onHoverBtnMouseMove(e.type)}
                  >
                    <img src={isHoverBtn === "submit" ? GreenBtnHover : GreenBtnDefault} alt=""/>
                    {claimingNft ? "BUSY" : "BUY"}
                  </StyledButton>
                </s.Container>
                <s.SpacerSmall/>
              </>
            )}
          </>
        )}
      </s.Container>
    </ResponsiveWrapper>
  );
};

export default MintBox;